export const Keywords = Array.from(new Set([
    ...Array.from(Cards.keys()),
    ...Array.from(Terms.keys()),
    ...Array.from(Units.keys()),
    ...Array.from(Upgrades.keys()),
]).keys());
const searcher = new AhoCorasick(Keywords);
function splitTextPiece(text) {
    let result = searcher.search(text).map(res => {
        let r = '';
        res.keys.forEach(s => {
            if (s.length > r.length) {
                r = s;
            }
        });
        return {
            start: res.end - r.length + 1,
            end: res.end,
            word: r,
        };
    });
    result.sort((a, b) => b.end - a.end);
    for (let i = 0; i < result.length; i++) {
        if (result[i].drop) {
            continue;
        }
        const outer = result[i];
        for (let j = i + 1; j < result.length; j++) {
            if (!result[j].drop &&
                outer.start <= result[j].start &&
                result[j].end <= outer.end) {
                result[j].drop = true;
            }
        }
    }
    result = result.filter(r => !r.drop);
    result.sort((a, b) => a.end - b.end);
    let ps = 0;
    const secs = [];
    result.forEach(res => {
        if (ps < res.start) {
            secs.push({
                t: 'str',
                s: text.substring(ps, res.start),
            });
        }
        secs.push({
            t: 'ref',
            s: res.word,
        });
        ps = res.end + 1;
    });
    if (ps < text.length) {
        secs.push({
            t: 'str',
            s: text.substring(ps),
        });
    }
    return secs;
}
export function splitText(text) {
    const result = [];
    text.split(/(?=<.+?>)|(?<=<.+?>)/).forEach(s => {
        const m = /<(.+?)>/.exec(s);
        if (m) {
            result.push(...splitTextPiece(m[1]).map(n => {
                n.m = true;
                return n;
            }));
        }
        else {
            result.push(...splitTextPiece(s));
        }
    });
    return result;
}
